.footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.footer .footerInfo {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.footer .footerInfo p {
    padding-left: 20px;
    font-size: 1rem;
    color: #000000;
    text-decoration: none;
    font-weight: 700;
    justify-content: center;
}

.footer .footerInfo .navigation ul {
    padding-left: 60px;
    display: flex;
    flex-direction: row;
}

.footer .footerInfo .navigation ul .listItem {
    list-style: none;
}

.footer .footerInfo .navigation ul .listItem a {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer .footerInfo .navigation ul .listItem a span svg {
    transition: 0.5s;
    max-height: 4rem;

    width: 6rem;
    height: 3rem;
}
.footer .footerInfo .navigation ul .listItem:hover a span svg {
    transform: translateY(-35px);
}

.footer .footerInfo .navigation ul .listItem a .listItemText {
    position: absolute;
    transition: 0.5s;
    opacity: 0;
    transform: translateY(20px);
}
.footer .footerInfo .navigation ul .listItem:hover a .listItemText {
    position: absolute;
    transition: 0.5s;
    opacity: 1;
    transform: translateY(10px);
}

.footer .logo {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.footer .logo img {
    padding: 0.5rem 1rem;
    max-height: 4rem;
}
