.header {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.header .logo {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.header .logo img {
    padding: 0.5rem 1rem;
    max-height: 4rem;
}

.header .logo .title {
    font-size: 1.5rem;
    color: var(--primary-orange);
    text-decoration: none;
    font-weight: 700;
}

.header .GitHub {
    padding: 0.5rem 1rem;
}

.header .GitHub a svg {
    max-height: 2rem;
    width: 50px;
    height: 50px;
}

.header .GitHub a svg:hover {
    fill: var(--primary-orange);
}


